"use client";

import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

type Parent = {
  id: string;
  fullname: string;
  contact_number: string;
  address: {
    purok: string;
    baranggay: string;
    municipality: string;
    province: string;
  };
  auth: {
    email: string;
  };
  lastLogin: string | null;
};

const ParentManagement = () => {
  const queryClient = useQueryClient();
  const { parentsList, updateParent, deleteParent } = useProtectedRoutesApi();
  const [editableData, setEditableData] = useState<{ [key: string]: Parent }>(
    {}
  );
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedBaranggay, setSelectedBaranggay] = useState("");

  const { data, isLoading, isError, error } = useQuery({
    queryKey: ["parents"],
    queryFn: parentsList,
  });

  // Get unique baranggays for dropdown
  const baranggays = Array.from(
    new Set(data?.data?.map((parent) => parent.address.baranggay) || [])
  );

  // Combined filter function
  const filteredParents =
    data?.data?.filter((parent) => {
      const matchesSearch = parent.fullname
        .toLowerCase()
        .startsWith(searchTerm.toLowerCase());
      const matchesBaranggay =
        !selectedBaranggay ||
        parent.address.baranggay.toLowerCase() ===
          selectedBaranggay.toLowerCase();
      return matchesSearch && matchesBaranggay;
    }) || [];

  // ... (keep existing mutation and handler functions unchanged)

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />
      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        <div className="flex flex-col gap-4">
          <h1 className="text-2xl font-bold">Parent Management</h1>

          <div className="flex gap-4 items-center">
            <Input
              placeholder="Search by name..."
              className="max-w-xs"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />

            <div className="flex items-center gap-2">
              <Label htmlFor="baranggay-filter" className="text-sm">
                Filter by Baranggay:
              </Label>
              <Select
                value={selectedBaranggay}
                onValueChange={(value) => setSelectedBaranggay(value)}
              >
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="All Baranggays" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="">All Baranggays</SelectItem>
                  {baranggays.map((baranggay) => (
                    <SelectItem key={baranggay} value={baranggay}>
                      {baranggay}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
        </div>

        {/* Keep existing table and other components unchanged */}
      </main>
      <Footer />
    </div>
  );
};

export default ParentManagement;
